const showPlnPostPaidReceipt = (
    data
) => {
    var _date = data.timestamp;
    var _status = data.status || '';
    var _name = data.details.nama;
    var _customerId = data.details.idpel;
    var _tarif = data.details.tarif;
    var _daya = data.details.daya;
    var _bill = data.details.tagihan;
    var _admin = data.details.admin;
    var _total = data.details.total;
    var _info = data.details.info;

    return (<List>
        <ListItem><ReceiptHeader icon={b1} title="PLN Pascabayar" date={_date} /></ListItem>
        <ListItem>
            <ListItemText primary={'Status Transaksi'} />
            <ListItemText primary={_status.toUpperCase()} style={{ textAlign: 'right' }} />
        </ListItem>
        <ListItem>
            <ListItemText primary={'Nama'} />
            <ListItemText primary={_name} style={{ textAlign: 'right' }} />
        </ListItem>
        <ListItem>
            <ListItemText primary={'ID Pelanggan'} />
            <ListItemText primary={_customerId} style={{ textAlign: 'right' }} />
        </ListItem>
        <ListItem>
            <ListItemText primary={'Tarif/Daya'} />
            <ListItemText primary={'$_tarif/$_daya'} style={{ textAlign: 'right' }} />
        </ListItem>
        <ListItem>
            <ListItemText primary={'Tagihan'} />
            <ListItemText primary={rupiah(_bill)} style={{ textAlign: 'right' }} />
        </ListItem>
        <ListItem>
            <ListItemText primary={'Biaya Admin'} />
            <ListItemText primary={rupiah(_admin)} style={{ textAlign: 'right' }} />
        </ListItem>
        <ListItem>
            <ListItemText primary={<b>{'Total'}</b>} />
            <ListItemText primary={<b>{rupiah(_total)}</b>} style={{ textAlign: 'right' }} />
        </ListItem>
        Divider(thickness: 2),
        Text(
        _info.replaceAll(';', '\n'),
        textAlign: TextAlign.center,
        style: Theme.of(context)
        .textTheme
        .bodyText1!
        .copyWith(fontWeight: FontWeight.bold),
        ),
    </List>);
}

